import React, { lazy, Suspense } from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import Auth from "./routes/Auth";

import {
  createBrowserRouter as Router,
  RouterProvider,
} from "react-router-dom";
import ErrorPage from "./routes/Error";

// import SignUp from './routes/SignUp'
// import Chat from "./routes/Chat";

import NoChat from "./routes/NoChat";
// import MessageBlock from "./routes/MessageBlock";
// import SignIn from './routes/SignIn';
import AppLayout from "./routes/AppLayout";

const SignIn = lazy(() => import("./routes/SignIn"));
const SignUp = lazy(() => import("./routes/SignUp"));

const Chat = lazy(() => import("./routes/Chat"));
const MessageBlock = lazy(() => import("./routes/MessageBlock"));

const router = Router([
  {
    path: "/chat",
    element: <App />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "auth",
        element: <Auth />,
        children: [
          {
            path: "signin",
            element: <SignIn />,
          },
          {
            path: "signup",
            element: <SignUp />,
          },
        ],
      },
      {
        path: "room",
        element: <AppLayout />,
        children: [
          {
            path: ":userId",
            element: <Chat />,
            children: [
              {
                path: "",
                element: <NoChat />,
              },
              {
                path: ":contactId",
                element: <MessageBlock />,
              },
            ],
          },
        ],
      },
    ],
  },
]);

/**
 * Handle sending state through use navigation hook
 * Use the useNavigate hook:

    const navigate = useNavigate();
    navigate('/other-page', { state: { id: 7, color: 'green' } });
    Then, you can access the state data in '/other-page' via the useLocation hook:

    const {state} = useLocation();
    const { id, color } = state; // Read values passed on state
 */

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <Suspense fallback={<div>Loading...</div>}>
    <RouterProvider router={router} />
  </Suspense>
);
