import React, { useEffect, useState } from "react";
import { Outlet, useMatch, useNavigate } from "react-router-dom";
import userService from "./services/user";
import messageService from "./services/messages";

const App = (props) => {
  const [user, setUser] = useState(null);

  const [contacts, setContacts] = useState([]);
  const [messages, setMessages] = useState(null);
  const navigate = useNavigate();

  const match = useMatch("chat");

  useEffect(() => {
    const keptUserObj = JSON.parse(localStorage.getItem("loggedUser"));

    if (keptUserObj) {
      setUser(keptUserObj.user);
      messageService.setToken(keptUserObj.token);
    }
  }, []);

  useEffect(() => {
    // This conditional statement checks if
    // Route strictly matches '/chat' route
    // So as not to allow the conditional statement
    // To extend to children routes
    if (match) {
      if (user) {
        navigate("room");
      } else {
        navigate("auth");
      }
    }
  }, [user, match, navigate]);

  // Fetch the contacts of the user
  useEffect(() => {
    if (!user) return;
    const getContacts = async () => {
      const fetchedContacts = await userService.getContactsOnLogin(
        user.username
      );

      setContacts(fetchedContacts);
    };
    getContacts();
  }, [user]);

  // Create a new message
  const createMessage = async (msg, person) => {
    if (!msg.string && !msg.files) {
      return;
    }

    // BUG ALERT TODO: it is not only one side that can create messages
    const messageObject = {
      string: msg.string,
      files: msg.files,
      sender: user.username,
      receiver: person.username,
    };
    try {
      const message = await messageService.createMessage(messageObject);
      setMessages(messages.concat(message));
    } catch (e) {
      console.error(e.message);
    }
  };

  const logOut = () => {
    localStorage.removeItem("loggedUser");
    setUser(null);
  };

  const getUserDetails = async (userObj) => {
    try {
      messageService.setToken(userObj.user.token);
      const contactss = await userService.getContactsOnLogin(
        userObj.user.username
      );
      setContacts(contactss);
    } catch (e) {
      console.error(e);
    }
  };

  const context = {
    userState: {
      user,
      setUser,
    },
    contactState: {
      contacts,
      setContacts,
    },
    messagesState: {
      messages,
      setMessages,
    },
    logOut,
    getUserDetails,
    createMessage,
  };

  return (
    <>
      <Outlet context={context} />
    </>
  );
};

export default App;
