import axios from "axios";
const baseUrl = "/api/messages";

let token = null;

const setToken = (newToken) => {
  token = `bearer ${newToken}`;
};

const getAll = async () => {
  const res = await axios.get(baseUrl);
  return res.data;
};

const getMessages = async (sender, receiver) => {
  const res = await axios.get(`${baseUrl}/fetch`, {
    params: {
      sender,
      receiver,
    },
  });
  return res.data;
};

const create = async (credentials) => {
  const config = {
    headers: { Authorization: token },
  };
  const res = await axios.post(baseUrl, credentials, config);
  return res.data;
};

const update = async (id, resource) => {
  const res = await axios.put(baseUrl + "/" + id, resource);
  return res.data;
};

const remove = async (id) => {
  const res = await axios.delete(baseUrl + "/" + id);
  return res.data;
};

export default {
  getAll,
  create,
  update,
  remove,
  setToken,
  getMessages,
};
